import React, { Component } from 'react';
import { connect } from 'react-redux';
import Search from './Search';
import IssuesList from './IssuesList';
import './../css/App.css';

export class App extends Component {
  render() {
    let content;
    let componentChildren = this.props.children;
    //console.log('App props = ', this.props);

    if (componentChildren) {
        content = this.props.children;
    }  else {
        content = (
            <div className="container">
                <div className="row">
                    <Search setIssues={this.props.onSetIssues} preload={this.props.onPreload}
                            setSelectList={this.props.onSetSelectList} selectList={this.props.selectList}
                            clearSelectList={this.props.onClearSelectList}/>
                </div>
                <div className="row">
                    <IssuesList issues={this.props.issues} preload={this.props.preload} />
                </div>
            </div>
        )
    }
      
    return (
        <div className="content">
            {content}
        </div>
    );
  }
}

export default connect(
    (state) => ({
        issues: state.issuesList,
        preload: state.preload,
        selectList: state.selectList
    }),
    dispatch => ({
        onSetIssues: (issues) => {
            dispatch({ type: 'SET_ISSUES', issues: issues})
        },
        onPreload: (preload) => {
            dispatch({ type: 'SET_PRELOAD', preload: preload})
        },
        onSetSelectList: (list) => {
            dispatch({ type: 'SET_SELECT_LIST', list: list})
        },
        onClearSelectList: () => {
            dispatch({ type: 'CLEAR_SELECT_LIST'})
        }
    })
)(App); 



