import React, { Component } from 'react';

export default class SelectList extends Component {
    chooseName = (event) => {
        const target = event.target;
        const name = target.innerText;

        this.props.setRep(name);
        this.props.clearList();
    };

    render() {
        const filterIssues = this.props.filterIssues && this.props.filterIssues.toLowerCase();
        const list = this.props.list;
        let listFilter;
        let listName;

        if (filterIssues && list) {
            listFilter = list.filter(item => {
                item = item.toLowerCase();

                return item.indexOf(filterIssues) !== -1;
            })
        } else if (list) {
            listFilter = list;
        }

        if (Array.isArray(listFilter)) {
            listName = listFilter.map( (item, i) => {
                return (
                    <li key={i}>{item}</li>
                )
            });
        } else {
            listName = '';
        }

        return (
            <ul className="select_list unstyled" onClick={this.chooseName}>
                {listName}
            </ul>
        )
    }
}


