import React, { Component } from 'react';
import SelectList from './SelectList';

class Search extends Component{
    constructor(props) {
        super(props);

        this.state = {
            name: 'facebookincubator',
            rep: 'create-react-app',
            validName: true,
            validRep: true,
            numberIssue: 100,
            selectShow: false
        }
    }

    errorView(node, valid) {
        if (!valid) {
            node.parentElement.classList.add('has-error');
        } else {
            node.parentElement.classList.remove('has-error');
        }
    }

    valid(value) {
        if (value && value.length > 0) {
            return true;
        }

        return false;
    }

    onChangeName = () => {
        const node = this.refs.name;
        const name = node.value;
        const valid = this.valid(name);

        this.errorView(node, valid);
        this.setState({ name: name , validName: valid});
    };

    onChangeRep = () => {
        const node = this.refs.rep;
        const rep = node.value;
        const valid = this.valid(rep);

        this.errorView(node, valid);
        this.setState({ rep: rep, validRep: valid }, this.viewListRepo);
        console.log('onChangeRep event');
    };

    viewListRepo() {
        this.getListRepositories()
            .then(response => {
                let reps = '';

                try {
                    reps = JSON.parse(response.response);
                    //console.log('viewListRepo reps = ', reps);
                } catch (e){
                    console.log(e);
                }

                if (!reps) return;

                const arRepsName = reps.map((item) => {
                    return item.name;
                });
                
                //console.log('arRepsName = ', arRepsName);
                
                this.props.setSelectList(arRepsName);                

            })
            .catch(error => {
                console.log(error);
            })
    }

    getListRepositories() {
        const { name } = this.state;

        if (!name) return;

        return new Promise((resolve) => {
            let url = `https://api.github.com/users/${name}/repos`;
            let xhr = new XMLHttpRequest();

            xhr.open('GET', url);

            xhr.send();

            xhr.onreadystatechange = () => {
                if (+xhr.readyState !== 4) return;

                resolve(xhr);
            }
        });
    }

    onChangeNumber = () => {
        const node = this.refs.num;
        const num = node.value;
        const valid = this.valid(num);

        this.errorView(node, valid);
        this.setState({ numberIssue: num });
    };

    getRep = ({ name, rep, number }) => {
        return new Promise((resolve, reject) => {
            //let url = 'https://api.github.com/repos/facebookincubator/create-react-app/issues?page=1&per_page=100';
            let url = `https://api.github.com/repos/${name}/${rep}/issues`
                +`?state=open&page=1&per_page=${number}`;
            let xhr = new XMLHttpRequest();

            xhr.open('GET', url);
            xhr.send();

            xhr.onerror = (e) => {
                console.log('onerror, e = ', e);
            };

            xhr.onreadystatechange = () => {
                if (+xhr.readyState !== 4) return;

                resolve(xhr);
            }

        });
    };

    onGetIssues = () => {
        const { name, rep, validName, validRep, numberIssue: number } = this.state;

        if (validName && validRep) {
            let btn = this.refs.btn;

            btn.innerText = 'Loading...';
            this.props.preload(true);

            this.getRep({ name, rep, number })
                .then(xhr => {
                    try{
                        const issuesObj = JSON.parse(xhr.response);

                        btn.innerText = 'Load issues';
                        this.props.preload(false);

                        //console.log('issuesObj = ', issuesObj);

                        if (issuesObj.message && issuesObj.message === 'Not Found') {
                            alert('Repository not found');
                            this.props.setIssues([]);
                            return;
                        }

                        this.props.setIssues(issuesObj);
                    } catch (e) {
                        console.log('func: onGetIssues, e: ', e);
                    }
                })
                .catch(xhr => {
                    alert('Repository not found');
                    btn.innerText = 'Load issues';
                    this.props.preload(false);
                });
        }
    };

    setNameRep = (rep) => {
        this.setState({rep: rep, validRep: true}, this.onGetIssues);
        this.errorView(this.refs.rep, true);
    }
    
    render() {
        //console.log('Search render()');

        return (
            <div className="col-lg-12">
                <div className="form-group">
                    <label htmlFor="exampleInputName">Имя пользователя</label>
                    <input type="text" className="form-control" value={this.state.name}
                           id="exampleInputName" placeholder="Имя" ref="name" onChange={this.onChangeName} />
                </div>
                <div className="form-group por_rel" ref="rep_cont">
                    <label htmlFor="exampleInputRep">Название репозитория</label>
                    <input type="text" className="form-control" value={this.state.rep}
                           id="exampleInputRep" placeholder="Репозиторий github" ref="rep"
                           onChange={this.onChangeRep} />
                    <SelectList list={this.props.selectList} setRep={this.setNameRep} 
                                clearList={this.props.clearSelectList} filterIssues={this.state.rep} />
                </div>
                <div className="form-group">
                    <label htmlFor="exampleInputNum">Количество issue для загрузки</label>
                    <input type="number" className="form-control" value={this.state.numberIssue}
                           id="exampleInputNum" placeholder="Количество issue для загрузки (default = 30)"
                           ref="num" onChange={this.onChangeNumber} />
                </div>
                <button type="submit" className="btn btn-default"
                        disabled={!(this.state.validName && this.state.validRep)}
                        onClick={this.onGetIssues} ref="btn">
                    Load issues
                </button>
            </div>
        )
    }
}

export default Search;


