import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router';

class DetailIssue extends Component {

    getIssue(issues) {
        const id = this.props.params.id;

        console.log('getIssue: ', id, issues);

        for (var i = 0; i < issues.length; i++) {
            let issue = issues[i];

            if(id == issue.id) {
                return issue;
            }
        }

        return false;
    }

    getContent(issue) {
        const { number, title, created_at, user, id, body } = issue;
        const created_date = (new Date (created_at)).toLocaleDateString();
        const url = `/detail/${id}`;

        return (
            <div className="issue-detail_page">
                <div>
                    <div><span className="issue-detail_page-title">№</span>{number}. </div>
                    <div className="list_issue-title">
                        <span className="issue-detail_page-title">Заголовок:</span> {title}.
                    </div>
                    <div>
                        <span className="issue-detail_page-title">Сообщение:</span> {body}
                    </div>
                </div>
                <div>
                    <span className="issue-detail_page-title">Дата создания:</span> {created_date}
                </div>
                <div className="user_git">
                    <div><span className="issue-detail_page-title">Автор:</span></div>
                    <div><img src={user.avatar_url + '&s=100'} alt={user.login}/></div>
                    <div className="user_git-name">
                        <span className="issue-detail_page-title">Логин:</span> {user.login}
                        <a href={user.html_url} target="_blank">Профиль</a>
                    </div>
                </div>
            </div>
        );
    }

    render() {
        console.log('DetailIssue, props = ', this.props);
        const issues = this.props.issues;
        const issue = this.getIssue(issues);
        const content = this.getContent(issue);

        console.log('issue detail = ', issue);


        return (
            <div className="container">
                <div className="row">
                    <div className="col-lg-12">
                        <div className="issue_nav"><Link to="/" >На главную</Link></div>
                        {content}
                    </div>
                </div>
            </div>
        )
    }
}

export default connect(
    (state) => ({
        issues: state.issuesList,
    }),
    dispatch => ({})
)(DetailIssue);



