import React, { Component } from 'react';
import Issue from './Issue';

class IssuesList extends Component{
    render(){
        //console.log('ListIssues props = ', this.props);
        //console.log('preload = ', this.props);
        let issues = this.props.issues;
        let issuesList;

        if (Array.isArray(issues)) {
            issuesList = issues.map((issue, i) => {
                return (
                    <Issue data={issue} key={i} />
                )
            });
        } else {
            issuesList='';
        }

        let preload = this.props.preload;
        let viewList = '';

        if (!preload) {
            viewList = <ul className="unstyled list_issue">{issuesList}</ul>;
        } else {
            viewList = <div className="preload"></div>
        }

        return (
            <div className="col-lg-12">
                {viewList}
            </div>
        )
    }
}

export default IssuesList;


