let initState = [];

function selectList(state = initState, action) {
    if (action.type === 'SET_SELECT_LIST') {
        
        return [...action.list];
    } else if (action.type === 'CLEAR_SELECT_LIST') {

        return []
    }
    
    return state;
}

export default selectList;


