import React, { Component } from 'react';
import { Link } from 'react-router'

export default class Issue extends Component {

    render() {
        //console.log('Issue = ', this.props);
        const { number, title, created_at, user, id } = this.props.data;
        const created_date = (new Date (created_at)).toLocaleDateString();
        const url = `/detail/${id}`;

        return (
            <li>
                <div>
                    <span> №{number}. </span>
                    <span className="list_issue-title">
                        <Link to={url} >{title}.</Link>
                    </span>
                    <span>Дата создания: {created_date}</span>
                </div>
                <div className="user_git">
                    <span><img src={user.avatar_url + '&s=40'} alt={user.login}/></span>
                    <span className="user_git-name">
                        <a href={user.html_url} target="_blank">{user.login}</a>
                    </span>
                </div>
            </li>
        )
    }
}




